body, html {
  background-color: #f5f5f5;
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
}

.chat-container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 10px;
  display: flex;
}

.chat-box {
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  width: 90%;
  max-width: 600px;
  height: 80vh;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px #0000001a;
}

.chat-messages li {
  color: #fff;
  word-wrap: break-word;
  background-color: #007bff;
  border-radius: 20px;
  max-width: 70%;
  margin-bottom: 10px;
  padding: 10px;
}

.chat-messages li.from-user {
  color: #fff;
  background-color: #007bff;
  align-self: flex-end;
  margin-left: auto;
}

.chat-messages li.from-bot {
  color: #000;
  background-color: #e0e0e0;
  align-self: flex-start;
  margin-right: auto;
}

.message-form {
  background: #eee;
  border-top: 1px solid #ddd;
  width: calc(100% - 20px);
  margin: 0;
  padding: 10px;
  display: flex;
}

.message-form input[type="text"] {
  border: none;
  border-radius: 5px;
  flex-grow: 1;
  margin-right: 10px;
  padding: 10px;
}

.message-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.message-form button:hover {
  background-color: #0056b3;
}

.bot-selector {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
}

.bot-selector select {
  color: #333;
  cursor: pointer;
  appearance: none;
  background-color: #fff;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2024%2024%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%3Cpath%20fill-rule%3D%22evenodd%22%20clip-rule%3D%22evenodd%22%20d%3D%22M7.41%208L12%2012.59%20L16.59%208%20L18%209.41%20L12%2015.41%20L6%209.41%20L7.41%208Z%22%20fill%3D%22%23AAAAAA%22%3E%3C/path%3E%3C/svg%3E");
  background-position: right 10px center;
  background-repeat: no-repeat;
  border: 2px solid #007bff;
  border-radius: 8px;
  outline: none;
  width: 50%;
  padding: 10px 15px;
  font-size: 16px;
}

.welcome-splash {
  background: #ffffffe6;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80%;
  margin-top: 9%;
  display: flex;
  position: absolute;
}

.chat-messages {
  flex-direction: column;
  flex-grow: 1;
  padding: 20px;
  list-style: none;
  display: flex;
  overflow-y: scroll;
}

.message-form {
  background: #eee;
  border-top: 1px solid #ddd;
  width: calc(100% - 20px);
  padding: 10px;
  display: flex;
}
